.button{
    background:#0077ff ;
    padding: 10px 20px;
    border: none;
    outline: none;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 auto;
    color: white;
    font-size: 18px;
    font-weight: bold;
    border-radius: 50px;
    cursor: pointer;
    transition: all .3s ease-in-out;

}
.button:hover{
    background: #014a97;
}
.arrow{
    margin-left: 10px;
}