.card{
    background: #1d1d1d;
    padding: 20px;
    cursor: pointer;
    border-radius: 10px;
}
.speakers{
    display: flex;
    align-items: center;
    position: relative;
    margin: 20px 0;
}

.avatars img{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    object-fit: cover;
    border: 2px solid #20bd5f;
    position: absolute;
    top: 0;
    left: 0;
    background: #1d1d1d;
}
.avatars img:last-child{
    top: 20px;
    left: 20px;
}
.names{
    margin-left: 100px;
}
.names span{
    display: inline-block;
    padding-bottom: 5px;
    margin-right: 5px;
}
.peopleCount{
    text-align: right;
}
.peopleCount span{
    font-weight: bold;
    margin-right: 5px;
}
.singleSpeaker .avatars img{
    position: intial;
}
.singleSpeaker .nameWrapper{
    margin-left: 20px;
}