.cardWrapper{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 6rem;
}
.message{
    font-weight: bold;
    font-size: 22px;
}
.spinner{
    margin-bottom: 20px;
    animation: spin 1s ease-in infinite;
}
@keyframes spin {
    100%{
        transform: rotate(360deg);
    }
}