.cardWrapper{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 6rem;
}
.actionButtonWrap{
    margin-top: 30px;
}
.subheading{
    color: #C4C5C5;
    text-align: center;
    margin-bottom: 20px;
}
.avatarWrapper{
    width: 110px;
    height: 110px;
    border: 6px solid #0077ff;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
}
.avatarImage{
    height: 90%;
    width: 90%;
    object-fit: cover;
}
.avatarInput{
    display: none;
}
.avatarLabel{
    color: #0077ff;
    margin: 20px 0;
    display: inline-block; 
}